-size:smaller;padding-top:20px;background:linear-gradient(to top, rgba(0,0,0,0.4) 30%, rgba(0,0,0,0) 100%);visibility:hidden;width:100%;text-align:center}.search-container{display:flex;flex-direction:row;margin-bottom:20px}.search-labels{margin-right:1em}.search-labels,.search-inputs{display:flex;flex-direction:column}.search-element{margin:0.3em}.top-bar{padding:1em;background-color:#999999;display:flex;flex-direction:column}.artwork{text-align:center}.zoom-buttons{text-align:center}.onoff{margin-left:5px;display:-moz-inline-stack;display:inline-block;vertical-align:middle;*vertical-align:auto;zoom:1;*display:inline;position:relative;cursor:pointer;width:55px;height:22px;line-height:23px;font-size:14px;font-family:"Helvetica Neue", Helvetica, Arial, sans-serif}.onoff label{position:absolute;top:0px;left:0px;width:100%;height:100%;cursor:pointer;background:#999999;border-radius:5px;font-weight:bold;color:#FFF;transition:background 0.3s, text-indent 0.3s;text-indent:27px;box-shadow:0px 0px 0px 1px rgba(0,0,0,0.4) inset}.onoff label:after{content:'NO';display:block;position:absolute;top:0px;left:0px;width:100%;font-size:12px;color:#000000;text-shadow:0px 1px 0px rgba(255,255,255,0.35);z-index:1}.onoff label:before{content:'';width:15px;height:16px;border-radius:3px;background:#FFF;position:absolute;z-index:2;top:3px;left:3px;display:block;transition:left 0.3s;box-shadow:0px 0px 5px rgba(0,0,0,0.4)}.onoff input:checked+label{background:#DDDDDD;text-indent:8px}.onoff input:checked+label:after{content:'YES';color:#091707}.onoff input:checked+label:before{left:37px}

